import React from 'react';
import {useDevice} from '../../hooks/useDevice/useDevice';
export interface ISocialSharingProps {
  /**
   *  Type of social sharing item
   */
  linkedin?: boolean;
  facebook?: boolean;
  whatsapp?: boolean;
  twitter?: boolean;
  /**
   *  Social media link
   */
  title: string;
  [others: string]: any;
}

const SocialSharing: React.FC<ISocialSharingProps> = (props) => {
  const {title, ...rest} = props;
  const {isMobile, isDesktop} = useDevice();
  const location = (isMobile || isDesktop) && window.location;

  const shareLink = (
    <div className="link_small" onClick={() => navigator.clipboard.writeText(`${location}`)}>
      {title}
      <span className="material-icons right">link</span>
    </div>
  );

  return (
    <div className="social-sharing" {...rest}>
      <a
        className="social-sharing-icon linkedin"
        href={`https://www.linkedin.com/shareArticle/?mini=true&url=${location}`}
        target="self"
      />
      <a className="social-sharing-icon twitter" href={`https://twitter.com/intent/tweet?text=${location}`} target="self" />
      <a
        className="social-sharing-icon facebook"
        href={`https://www.facebook.com/sharer/sharer.php?u=${location}`}
        target="self"
      />
      <a className="social-sharing-icon whatsapp" href={`whatsapp://send?text=${location}`} target="self" />
      {shareLink}
    </div>
  );
};

export default SocialSharing;
